@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

.navbar {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  color: #fff;
  font-weight: 700;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
  color: green;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 30px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover,
.current {
  border-bottom: 4px solid green;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }
  .fa-typo3 {
    font-size: 1rem;
    color: green;
  }
  .navbar-logo {
    font-size: 1.5rem;
  }
  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.card-container {
  max-width: 300px;
  max-height: 400px;
  background-color: white;
  border-radius: 15px;
  padding-bottom: 15px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  outline: none;
  overflow: auto;
}

.img-container {
  max-height: 40%;
  width: 100%;
  border-radius: 15px;
  overflow: hidden;
  border-bottom: 3px solid black;
}
.img-container img {
  border-radius: 10px;
  width: 300px;
  height: 180px;
  transition: all 0.5s ease-out;
  outline: none;
}
.img-container img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.description {
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 60%;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  text-align: center;
}
.card-container::-webkit-scrollbar {
  width: 0px;
}
.description p {
  font-size: 1rem;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}
.description h1 {
  font-size: 1.5rem;
  font-weight: 700;
  /* text-decoration: underline; */
  text-transform: uppercase;
}

.wrapper {
  padding: 10px;
  width: 100%;
  height: -webkit-max-content;
  height: max-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
  background-color: #0b0c10;
}

.hero-title {
  margin-top: 50px;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.hero-title h1 {
  font-size: 3.5rem;
  color: rgb(50, 197, 70);
  text-transform: capitalize;
}
.card-collection {
  width: 100%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  grid-gap: 50px;
  gap: 50px;
  padding: 50px;
  flex-wrap: wrap;
}

@media screen and (max-width: 960px) {
  .hero-title h1 {
    font-size: 2rem;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --heights: 50vh;
  --width: 100%;
}

.slider-container {
  height: 50vh;
  height: var(--heights);
  width: 100%;
  width: var(--width);
  position: relative;
  margin: auto;
  overflow: hidden;
}

.active {
  display: inline-block;
}
.inactive {
  display: none;
}
.slides {
  height: 50vh;
  height: var(--heights);
  width: 100%;
  width: var(--width);
  position: relative;
}

.slides-image {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: contain;
}
.slide-title,
.slide-text {
  width: 100%;
  height: 100%;
  position: absolute;
  color: #dae63f;
  font-size: 50px;
  text-align: center;
  top: 40%;
  font-weight: 900;
  z-index: 10;
}

.slide-text {
  font-size: 2rem;
  top: 65%;
}
.prev,
.next {
  cursor: pointer;
  z-index: 100;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 1rem;
  margin-top: -3rem;
  font-size: 40px;
  font-weight: bold;
  border-radius: 0px 5px 5px 0px;
}

.prev:hover,
.next:hover {
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease-in;
}

.next {
  border-radius: 5px 0px 0px 5px;
  right: 0;
}

.all-dots {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  top: 85%;
  justify-content: center;
  z-index: 200;
}

.dot {
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  margin: 0px 3px;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  display: inline-block;
}

.dot-active,
.dot:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

@media screen and (max-width: 960px) {
  .slide-title,
  .slide-text {
    width: 100%;
    height: 100%;
    position: absolute;

    font-size: 30px;
    text-align: center;
    top: 40%;
    z-index: 10;
  }

  .slide-text {
    font-size: 1rem;
    top: 65%;
  }
}

.footer-container {
  background: rgb(0, 0, 0);
  height: 30vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.pages,
.contact {
  height: 75%;
  width: 20%;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
  grid-gap: 5px;
  gap: 5px;
}
.pages h2,
.contact h2 {
  border-bottom: 2px solid white;
  text-align: center;
}

li {
  list-style: none;
  margin-bottom: 10px;
}

a {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.664);
}
a:hover {
  color: green;
}

@media screen and (max-width: 960px) {
  .footer-container {
    flex-direction: column;
    height: -webkit-max-content;
    height: max-content;
  }
  .pages,
  .contact {
    height: 40%;
    width: 80%;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
    grid-gap: 5px;
    gap: 5px;
    margin: 30px;
  }
  .pages h2,
  .contact h2 {
    border-bottom: 2px solid white;
    text-align: center;
    margin-bottom: 10px;
  }
}

.copyright-container {
  max-width: 100%;
  max-height: 40px;
  background: black;
  display: flex;
  align-items: center;
  justify-content: center;
}
.copyright-container p {
  font-size: 0.6rem;
  color: white;
  opacity: 0.6;
  letter-spacing: 1.5px;
  padding-bottom: 2px;
}

.signup-container {
  height: 90vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #3a626e;
}
.form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-gap: 30px;
  gap: 30px;
  width: 70%;
  height: 100%;
  background-color: #3a626e;

  background-position: center;
  background-size: cover;
}
.form h2 {
  font-size: 3rem;
  font-weight: 900;
  color: black;
  z-index: 100;
  border-bottom: 5px solid #000;
}
.form img {
  position: absolute;

  opacity: 0.5;
}
form {
  height: 70%;
  width: 60%;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  display: flex;
  padding: 50px;
  justify-content: space-evenly;
  flex-direction: column;
}
form label {
  display: block;
  margin-left: 100px;
  font-size: 1.1rem;
  font-weight: 800;
  /* color: white; */
}
form input,
form select {
  margin-bottom: 20px;
  margin-left: 20px;
  width: 250px;
  height: 30px;
  border: none;
  border-radius: 0.5rem;
  padding-left: 10px;
  font-size: 12px;
  font-weight: 600;
  background-color: rgb(223, 223, 223);
  color: #000;
  opacity: 0.8;
}
form input:focus {
  opacity: 1;
}
form button {
  width: 150px;
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 1.2rem;
  border: 0;
  margin-top: 10px;
  background-color: #000;
  color: #3a626e;
  /* color: #fff; */
  transition: all 0.3s ease-in;
}
form button:hover {
  background: #3a626e;
  cursor: pointer;
  color: white;
}

@media screen and (max-width: 1300px) {
  html {
    overflow-x: hidden;
  }
  .form {
