* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --heights: 50vh;
  --width: 100%;
}

.slider-container {
  height: var(--heights);
  width: var(--width);
  position: relative;
  margin: auto;
  overflow: hidden;
}

.active {
  display: inline-block;
}
.inactive {
  display: none;
}
.slides {
  height: var(--heights);
  width: var(--width);
  position: relative;
}

.slides-image {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: contain;
}
.slide-title,
.slide-text {
  width: 100%;
  height: 100%;
  position: absolute;
  color: #dae63f;
  font-size: 50px;
  text-align: center;
  top: 40%;
  font-weight: 900;
  z-index: 10;
}

.slide-text {
  font-size: 2rem;
  top: 65%;
}
.prev,
.next {
  cursor: pointer;
  z-index: 100;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 1rem;
  margin-top: -3rem;
  font-size: 40px;
  font-weight: bold;
  border-radius: 0px 5px 5px 0px;
}

.prev:hover,
.next:hover {
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease-in;
}

.next {
  border-radius: 5px 0px 0px 5px;
  right: 0;
}

.all-dots {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  top: 85%;
  justify-content: center;
  z-index: 200;
}

.dot {
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  margin: 0px 3px;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  display: inline-block;
}

.dot-active,
.dot:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

@media screen and (max-width: 960px) {
  .slide-title,
  .slide-text {
    width: 100%;
    height: 100%;
    position: absolute;

    font-size: 30px;
    text-align: center;
    top: 40%;
    z-index: 10;
  }

  .slide-text {
    font-size: 1rem;
    top: 65%;
  }
}
